.contenedor-HomeBusiness {
  width: 100vw;
  margin-top: 40px;
}

.section-one-HomeBusiness {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin-bottom: -20%;
}

/* style button */
.btn-warning {
  border: 0;
  padding: 10px 15px;
  font-weight: 600;
  border-radius: 50px;
}

.section-two-HomeBusiness,
.ContentCardsGlobalHomeBusiness {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

/* section3 */
.section-3-Homebusiness {
  margin-top: -7%;
  background-color: #2f2f2f;
  padding-bottom: 50px;
}

.section3HomeBusiness,
.section4HomeBusiness {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.imageSection3HomeBusiness,
.InputsSection3HomeBusiness {
  width: 100%;
}

.section5HomeBusiness,
.buttonsSection5HomeBusiness,
.contentsection6HomeBusiness {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 40px;
}

.iconHomeBusiness11 {
  color: #f3b601;
}

.buttonsSection5HomeBusiness .btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  border: 1px solid #6c757d;
  border-radius: 20px;
  width: 200px;
  height: 300px;
  padding: 40px 50px;
  font-size: 20px;
  color: rgb(182, 182, 182);
  background-color: transparent;
}

/*  hover */
.buttonsSection5HomeBusiness .btn:hover img {
  filter: brightness(0) saturate(100%) invert(84%) sepia(24%) saturate(1029%)
    hue-rotate(355deg) brightness(98%) contrast(91%);
  transform: scale(1.05);
}

.buttonsSection5HomeBusiness .btn:hover {
  border: 0;
  box-shadow: rgb(0 0 0 / 15%) 0px 4px 11.83px;
}

.buttons-section-five {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 20px;
  width: 100%;
  justify-items: center;
}

.buttons-section-five .btn {
  width: 160px;
  height: 200px;
  padding: 25px;
  background-color: transparent;
  color: #9e9e9e;
  border: 1px solid #9e9e9e;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.buttons-section-five .btn:active {
  background-color: transparent;
}

.buttons-section-five .btn img {
  width: 100%;
}

.buttons-section-five .btn:hover img {
  filter: brightness(0) saturate(100%) invert(84%) sepia(24%) saturate(1029%)
    hue-rotate(355deg) brightness(98%) contrast(91%);
  transform: scale(1.05);
}

.buttons-section-five .btn:hover {
  box-shadow: 0px 4px 11.83px rgba(0, 0, 0, 0.15);
  border: 0;
}

/* card section2 */
.cardSecrtion2HomeB:hover {
  cursor: pointer;
  scale: 1.03;
}

@media screen and (min-width: 1024px) {
  .section-one-HomeBusiness {
    flex-direction: row;
    margin-bottom: -15%;
  }

  .section-one-HomeBusiness img {
    width: 50% !important;
  }

  .section3HomeBusiness,
  .section4HomeBusiness {
    flex-direction: row;
  }

  .ContentCardsGlobalHomeBusiness,
  .buttonsSection5HomeBusiness {
    flex-direction: row;
    gap: 50px;
  }

  .contentsection6HomeBusiness {
    flex-direction: row;
  }
}

@media screen and (min-width: 830px) {
    .buttons-section-five{
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
}
